body {
  background-color: rgb(44, 44, 44);
  font-size: 44px
  
}

.wrapper {
     color:rgb(187, 226, 211);
    display: flex;
    height: 100vh;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.wrapper h2 {
  margin: 0;
}

.break-session-length {
    display: flex;
    width: 900px;
    justify-content: space-between;
    
}


.break-session-length div div{

  margin-left: 120px;
}

button {
  border: none;
  padding: 8px;
  font-size: 28px;
  border-radius: 20px;
}

div > button {
  color: white;
  background-color: rgb(129, 151, 194);
}

div button:nth-child(2) {
  color: #000;
  background-color: rgb(141, 47, 52);
}

strong {
  margin: 0 8px;
}

.timer {
  border: 6px solid #2a2663;
  border-radius: 10px;
  text-align: center;
  margin: 20px 0;
  padding: 10px 30px;
}

.timer-wrapper > button {
  margin-left: 10px
}